import request from '@/utils/request'
	
// 商品接口
	// todo
	export function gdetail(data) {
	    return request({
	      	url: '/user/good/detail',
	      	method: 'get',
	      	params:data
	    })
	}

	export function gpage(data) {
	    return request({
	      	url: '/user/good/page',
	      	method: 'get',
	      	params:data
	    })
	}

// 商品分类接口
	export function glist(data) {
	    return request({
	      	url: '/user/goodCategory/list',
	      	method: 'get',
	      	params:data
	    })
	}